import React, { useEffect, useCallback } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import Wrap from 'components/wrap';
import { CrewCard } from 'components/cards';
import Form from 'components/form';
import { Header2 } from 'components/typography';
import Button from 'components/button';
import useStyles from './use-styles';
import { getTeamAll } from 'actions/team';

function Team() {
	const dispatch = useDispatch();
	const { team } = useSelector((state) => state.TeamAll);

	const styles = useStyles();
	const fetchTeamAll = useCallback(() => dispatch(getTeamAll()), [dispatch]);

	useEffect(() => {
		fetchTeamAll();
	}, []);

	const bosses = team.filter(
		(item) => item.slug === 'paul_chudleigh' || item.slug === 'michael_sastry'
	);
	const employees = team.filter(
		(item) => item.slug !== 'paul_chudleigh' && item.slug !== 'michael_sastry'
	);

	console.log(bosses);

	return (
		<section className={styles.teamPage}>
			<Wrap>
				<div className={styles.topWrap}>
					<Header2 className={styles.topHeader} title='Our team' />
					<Button title='Join to Us' />
				</div>

				<div className={styles.topRow}>
					{bosses &&
						bosses.map((item) => (
							<CrewCard name={item.name} bio={item.roles} url={item._id} />
						))}
				</div>
				<div className={styles.teamWrap}>
					{employees &&
						employees.map((item) => {
							return (
								<CrewCard
									name={item.name}
									bio={item.roles}
									photos={[item.imageKeys.professional, item.imageKeys.smiley]}
									url={item._id}
								/>
							);
						})}
				</div>
			</Wrap>
			<ContactForm />
		</section>
	);
}

function ContactForm() {
	const styles = useStyles();
	return (
		<div className={styles.contacts}>
			<Wrap>
				<Header2 title='Get in touch' />
				<Form />
			</Wrap>
		</div>
	);
}

export default Team;
